export default "/* COMMENTED CATEGORIES AND TAGS\nimport React from 'react'\nimport PropTypes from 'prop-types'\nimport {Link, graphql} from 'gatsby'\nimport { markdownSlug } from 'components/snippets/markdown-slug.js'\n\nimport SEO from 'components/seo'\nimport Layout from 'components/layout'\n\nclass Template extends React.Component {\n  render() {\n    const {data, pageContext} = this.props\n    const seo = {};\n    seo.title = 'Categories'\n    seo.description = 'Description'\n    return (\n      <Layout seo={seo} page={data}>\n        <SEO title={seo.title + '  ' + paseoge.description} />\n        {data.categories.category.map((category, i) => (\n          <div key={i}>\n            <Link to={`/docs/${kebabCase(category.title)}/`}>\n              {category.title}\n            </Link>\n          </div>\n        ))}\n        {data.allMarkdownRemark.posts.map(({post}, i) => (\n          <div key={i}>\n            <Link to={markdownSlug(post)}>\n              {post.frontmatter.title}\n            </Link>\n          </div>\n        ))}\n      </Layout>\n    )\n  }\n}\n\nTemplate.propTypes = {\n  data: PropTypes.shape({\n    categories: PropTypes.shape({\n      category: PropTypes.arrayOf(\n        PropTypes.shape({\n          title: PropTypes.string.isRequired,\n        }).isRequired\n      ),\n    }),\n    allMarkdownRemark: PropTypes.shape({\n      posts: PropTypes.arrayOf(\n        PropTypes.shape({\n          post: PropTypes.shape({\n            frontmatter: PropTypes.shape({\n              title: PropTypes.string.isRequired,\n              categories: PropTypes.array.isRequired,\n            }).isRequired,\n          }).isRequired,\n        }).isRequired,\n      ),\n    }).isRequired,\n  }).isRequired,\n}\n\nexport default Template\n\nexport const query = graphql`\n  query($category: String!) {\n    categories: allMarkdownRemark(sort: {fields: [frontmatter___date], order: DESC}) {\n      category: group(field: frontmatter___categories) {\n        title: fieldValue\n      }\n    }\n    allMarkdownRemark(filter: {frontmatter: {categories: {eq: $category}}}) {\n      posts: edges {\n        post: node {\n          frontmatter {\n            title\n            categories\n          }\n        }\n      }\n    }\n  }\n`\n*/\n"