export default "import { Xt } from 'xtend-library'\nimport 'xtend-library/src/vars.js'\nimport 'xtend-library/src/extensions/slider/slider.js'\n\nXt.mount.push({\n  matches: '.demo--2020-brands',\n  mount: object => {\n    // slider\n\n    let self = new Xt.Slider(object, {\n      auto: {\n        timeInitial: 0,\n      },\n      align: 'left',\n      groupMq: false,\n      autoHeight: false,\n      drag: {\n        wrap: true,\n      },\n    })\n\n    // on\n\n    const eventOn = e => {\n      const tr = e.target\n      // useCapture delegation\n      if (self.targets.includes(tr)) {\n        // set auto time\n        const draggerWidth = self.dragger.offsetWidth\n        const slideWidth = tr.offsetWidth\n        let time\n        if (object.classList.contains('demo--2020-brands--constant')) {\n          time = (slideWidth / draggerWidth) * 50000\n        } else {\n          time = (draggerWidth / slideWidth) * 100000\n        }\n        self.options.auto.time = time\n        self.object.querySelector('.slides-inner').style.transitionDuration = time + 'ms'\n      }\n    }\n\n    self.object.addEventListener('on.xt', eventOn, true)\n\n    // unmount\n\n    const unmount = () => {\n      self.destroy()\n      self = null\n    }\n    return unmount\n  },\n})\n"